.marquee {
  white-space: nowrap;
}

.marquee__inner {
  display: flex;
}

.marquee__content {
  display: flex;
  align-items: center;
}

.breaking_news.marquee {
  overflow: hidden;
}

.wcf-breaking-news-title {
  margin-left: 20px;
  margin-right: 20px;
}

.breaking_news_icon {
  width: 20px;
  text-align: center;
}

.breaking_news_icon svg {
  font-size: 20px;
}